// Utility Color Classes

$light-contrast-color: rgba(255, 255, 255, 0.87) !default;
$dark-contrast-color: rgba(0, 0, 0, 0.87) !default;
$strong-light-contrast-color: rgb(255, 255, 255) !default;

$foreground-light: (
  '1': rgba(255, 255, 255, 1.0),
  '2': rgba(255, 255, 255, 0.7),
  '3': rgba(255, 255, 255, 0.5),
  '4': rgba(255, 255, 255, 0.12),
  'shadow': '1px 1px 0px rgba(0, 0, 0, 0.4), -1px -1px 0px rgba(0, 0, 0, 0.4)'
) !default;

$foreground-dark: (
  '1': rgba(0, 0, 0, 0.87),
  '2': rgba(0, 0, 0, 0.54),
  '3': rgba(0, 0, 0, 0.38),
  '4': rgba(0, 0, 0, 0.12),
  'shadow': none
) !default;

$color-red: (
  '50': #ffebee,
  '100': #ffcdd2,
  '200': #ef9a9a,
  '300': #e57373,
  '400': #ef5350,
  '500': #f44336,
  '600': #e53935,
  '700': #d32f2f,
  '800': #c62828,
  '900': #b71c1c,
  'A100': #ff8a80,
  'A200': #ff5252,
  'A400': #ff1744,
  'A700': #d50000,
  'contrastDefaultColor': $light-contrast-color,
  'contrastDarkColors': ('50', '100', '200', '300', 'A100'),
  'contrastStrongLightColors': ('400', '500', '600', '700', 'A200', 'A400', 'A700')
) !default;

