                                    }
                                }

                                input {
                                    background-color: #fff;
                                    border: solid 1px #d4d4d4;
                                }

                                md-input-container {
                                    margin-top: 10px !important;
                                }
                            }
                        }

                        .empty-list {
                            text-align: center;
                        }

                        .section-header {
                            background-color: #ebeff3;
                            min-height: 40px;

                            &:hover {
                                .actions {
                                    display: block;
                                }
                            }

                            &.blur {
                                opacity: .3;

                                .actions {
                                    display: none !important;
                                }
                            }

                            .section-title {
                                flex: 20;
                                padding: 10px;

                                b:last-child {
                                    display: none;
                                }
                            }

                            .actions {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }

    .drag-indicator {
        display: flex;
        align-items: center;

        .svg-icon {
            width: 33px;
            height: 24px;
        }
    }

    .type-icon {
        position: relative;
        max-width: 45px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30%;

        div {
            border-radius: 5px;
            width: 32px;
            height: 32px;
            display: flex;
            justify-content: center;
            align-items: center;

            .svg-icon {
                width: 24px;
                height: 24px;
            }
        }
    }

    .field-types {
        background-color: #f9f9f9;

        .header {
            padding: 10px;
        }

        &.blur {
            opacity: .3;

            .item {
                cursor: default !important;
            }

            .actions {
                display: none !important;
            }
        }

        .types-list {
            .item {
                position: relative;
                background-color: #fff;
                border: solid 2px #d4d4d4;
                margin: 5px;
                cursor: pointer;

                .type-title {
                    flex: 20;

                    .label {
                        color: #606a73;
                        padding: 2px;
                    }

                    .description {
                        color: #d4d4d4;
                        padding: 2px;
                    }
                }

                .actions {
                    position: absolute;
                    right: 0;
                    top: 0;
                    background-color: #fff;
                    display: none;
                }

                &:hover {
                    .actions {
                        display: block;
                    }
                }
            }

        }
    }

    .-placeholderAfter {
        padding-bottom: 13px;
    }

    .-placeholderBefore {
        padding-top: 13px;
    }

    .-isEmpty {
        padding-bottom: 40px;
    }

    .-isEmpty:before,
    .-placeholderBefore:before,
    .-placeholderAfter:before {
        background-color: #a0a0a0;
        height: 3px;
        width: 100%;
        content: ' ' !important;
        margin-top: 5px !important;
        margin-bottom: 5px !important;
    }
}

.popup.max-large {
    width: 70%;
    min-width: 70%;
    max-width: 70%;
}

.merge-guest-popup {
    background: #f5f5f5;
    display: flex;
    margin-top: 20px;

    .row-merge {
        flex: 1;

        > div {
            border-bottom: 1px solid rgba(0, 0, 0, .15);
            padding: 10px;

            &:last-child {
                border: none;
            }
        }

        &:nth-child(2), &:nth-child(3) {
            > div {
                color: #1E88E5;
                cursor: pointer;
                position: relative;

                &.active {
                    &:after {
                        position: absolute;
                        right: 10px;
                        top: 5px;
                        content: "\2714";
                    }
                }
            }
        }

        &:nth-child(2) .title-avatar {
            &:after {
                position: absolute;
                content: '\279C' !important;
                top: 30px !important;
                right: 5px !important;
                font-size: 16px;
            }
        }

        &:nth-child(3) .title-avatar {
            &:after {
                position: absolute;
                content: '\22B6' !important;
                top: 30px !important;
                right: -8px !important;
                font-size: 20px;
            }
        }

        &:nth-child(4) {
            background: #fff;
            pointer-events: none;
        }
    }

    .title-avatar {
        align-items: center;
        color: #656a72 !important;
        height: 60px;

        .md-avatar {
            margin-right: 10px;
        }

        b {
            font-size: 14px;
        }

        p {
            font-size: 11px;
            margin: 3px 0;
        }
    }
}

.pell-editor-wrap {
    background: #f5f5f5;
    color: #bdbac2;
    border-radius: 6px;
    height: 300px;

    .pell-actionbar {
        border: none;
    }
}

.no-messages {
    align-items: center;
    justify-content: center;
    display: flex;
    height: 100%;
    width: 100%;

    &._escalation, &._response {
        position: absolute;
    }
}

// tag-input component
.emberTagInput {
    margin: 0;
    padding: 5px 5px 2px 5px;
    border: 1px solid lightgray;
    cursor: text;
}

.emberTagInput--readOnly {
    cursor: default;
}

.emberTagInput li {
    list-style-type: none;
    display: inline-block;
}

.emberTagInput-tag,
.emberTagInput-input {
    font: 12px sans-serif;
    padding: 5px 10px;
    margin-bottom: 3px;
}

.emberTagInput-tag {
    background: cornflowerblue;
    border-radius: 20px;
    margin-right: 5px;
    color: white;
}

.emberTagInput-tag--remove {
    opacity: 0.75;
}

.emberTagInput-remove:before {
    content: 'x';
    cursor: pointer;
}

.emberTagInput-input {
    border: none;
    padding-left: 0;
}

.emberTagInput-input:focus {
    outline: none;
}

.emberTagInput-input.is-disabled {
    display: none;
}

.end-justify-align {
    display: flex;
    justify-content: flex-end;

    .secondary-2-button {
        margin-right: 15px;
    }
}

.upper-text {
    text-transform: uppercase;
}

.buttons-panel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 36px;
    margin-top: 20px;

    .action-buttons {
        svg {
            width: 20px;
            height: 20px;
            vertical-align: middle;
        }
    }

    .secondary-2-button, .primary-button {
        min-width: auto;
        margin-left: 0;
        margin-right: 15px;

        &.active,
        &.active:hover {
            background-color: #EFFAFF !important;
            color: $Secondary2ButtonActiveColor !important;
            border-color: $Secondary2ButtonActiveColor !important;

            svg {
                fill: $Secondary2ButtonActiveColor;
            }
        }
    }

    .navigate-button {
        padding: 3px !important;
        min-height: auto !important;
        min-width: auto !important;
        width: 30px !important;
        height: 30px !important;
        margin: 0 !important;
        background: none !important;
        border: none !important;

        svg {
            width: 24px;
            height: 24px;
            fill: $TextLightGreyColor;
        }

        &.active {
            svg {
                fill: $Secondary2ButtonActiveColor;
            }
        }

        .arrow-circle-icon {
            height: 24px;
            width: 24px;

            &._right {
                transform: rotate(180deg);
            }
        }

        .arrow-icon {
            height: 16px;
            transform: rotate(270deg);
            width: 16px;

            &._right {
                transform: rotate(90deg);
            }
        }
    }
}

::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

::-webkit-scrollbar-thumb {
    background: #9e9e9e;
}

::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0);
}