$primary-color-grey: #989a9f;

.header-menu {
    display: flex;
    justify-content: space-evenly;

    .item-link {
        color: #989a9f;
        font-weight: 400;
        font-size: 13px;
        display: flex;
        position: relative;

        &.active {
            color: #2b78e4;

            svg {
                fill: #2b78e4;
            }
        }
    }

    .direct-icon, .inbox-icon, .build-icon, .people-icon {
        height: 16px;
        margin-right: 8px;
        width: 16px;
    }

    .circle-item {
        background: #ff9900;
        border-radius: 50%;
        height: 6px;
        left: 14px;
        position: absolute;
        width: 6px;
    }
}

.chat-wrapper {
    display: flex;
    flex: 1;

    *, ::after, ::before {
        box-sizing: border-box;
    }

    .icon-button {
        display: flex;

        &:hover {
            background: none !important;
        }
    }

    .divider {
        text-align: center;
        margin: 15px;
        position: relative;

        &:before {
            border-top: 1px solid #eeecec;
            content: '';
            position: absolute;
            width: 100%;
            left: 0;
            top: 12px;
        }

        span {
            font-size: 11px;
            color: #989a9f;
            display: inline-block;
            position: relative;
            z-index: 1;
            background: #f9fdff;
            padding: 0 30px;
        }
    }

    .arrow-icon {
        transition: .2s;
        transform: rotate(90deg);
        height: 16px;
        width: 16px;
    }

    .cp-Panel-toggle {
        color: #121416;
        display: flex;
        justify-content: space-between;
        font-weight: 500;
        padding: 10px 0;

        &.team-members,
        &.pinned-items {
            padding-bottom: 10px;
        }

        &.cp-is-open {
            .arrow-icon {
                transform: rotate(0deg);
            }

            .svg-icon {
                fill: #2b78e4;
            }

            .menu-link {
                font-weight: 700;
                color: #2b78e4;
            }
        }

        > .unread .menu-link {
            > span {
                font-weight: 700;
                color: #2b78e4;
            }

            > svg {
                fill: #2b78e4;
            }
        }
    }

    .actions-input {
        bottom: 10px;
        display: flex;
        align-items: center;
        position: absolute;
        right: 20px;

        > span {
            cursor: pointer;
            margin-left: 10px;

            &:first-child {
                margin: 0;
            }
        }

        .svg-icon {
            height: 18px;
            width: 18px;
        }
    }

    .chat-input {
        padding: 10px;
        position: relative;

        .format-text-wrap {
            &.active > .svg-icon {
                fill: #2b78e4;
            }
        }

        .form-send-button {
            &[disabled] {
                pointer-events: none;
            }
        }

        .reply-icon {
            bottom: 4px;
            cursor: pointer;
            position: absolute;
            left: 3px;
            width: 39px;
            height: 35px;
            text-align: center;
            line-height: 40px;
            border-right: 1px solid rgba(218, 218, 218, 1);

            .svg-icon {
                height: 16px;
                width: 16px;
            }

            &:hover > .svg-icon {
                fill: #2b78e4;
            }
        }

        .wrapper-icon-chat {
            left: 14px;
            bottom: 100px;
            right: auto;
            top: auto;
        }

        .popup-open {
            z-index: 3;

            .apps-popup {
                display: block;
            }
        }

        .action-field {
            display: flex;
            margin: 5px 0 10px;
        }

        .reply-tab {
            color: #606a73;
            font-weight: 500;
            //font-size: 11px;

            span {
                cursor: pointer;
                display: inline-block;
                margin-right: 14px;
                padding: 0 10px 3px;

                &.disabled {
                    pointer-events: none;
                    opacity: .3;
                }

                &.active {
                    color: #2B78E4;
                    pointer-events: none;
                    border-bottom: 2px solid;
                }
            }
        }

        md-input-container {
            padding: 0;
            display: block;
        }

        textarea {
            overflow: scroll;
            padding: 14px 127px 0 50px;
        }

        textarea, .txt-input {
            background: #fff;
            border: 2px solid #dadada;
            border-radius: 5px;
            line-height: 18px;
            height: auto !important;
            min-height: 50px;
            max-height: 75px;
        }

        .action-text-editor {
            background-color: #fff;
            border-radius: 5px 5px 0 0;
            border: 2px solid #dadada;
            border-bottom: none;
            padding: 5px;

            & + .txt-input {
                border-radius: 0 0 5px 5px;
            }

            .md-button {
                border-radius: 5px;
                cursor: pointer;
                height: 30px;
                margin: 0 5px;
                line-height: normal;
                min-width: auto;
                width: 30px;

                &.md-focused {
                    background-color: transparent;
                }

                &.active, &:hover {
                    background-color: #f0f0f0 !important;
                }
            }
        }

        .wrap-input {
            position: relative;

            &.reply .txt-input {
                background: #fff5f5;
            }

            &.internal {
                .txt-input {
                    padding-left: 10px;
                }
            }

            .txt-input {
                padding: 15px 127px 10px 50px;
                outline: none;
                overflow: auto;

                > * {
                    display: inline-block;
                    vertical-align: middle;
                }

                .user-group {
                    background-color: #f2c4b2;
                    border-radius: 5px;
                    color: #989a9f;
                    pointer-events: none;
                    outline: none;
                    padding: 2px 4px;
                }
            }

            .txt-input[placeholder]:empty::before {
                content: attr(placeholder);
                color: #989a9f;
            }

            .txt-input[placeholder]:empty:focus::before {
                content: " ";
            }
        }

        .calendar-icon {
            display: none;
        }
    }

    .chat-calendar {
        visibility: hidden;
        display: none;
    }

    .wrap-user-check {
        max-height: 500px;
        overflow-y: auto;
        padding-top: 0;
    }
}

.tabs-message {
    border-right: 1px solid rgba(218, 218, 218, .5);
    width: 20%;
    overflow-x: hidden;

    *, ::after, ::before {
        box-sizing: border-box;
    }

    .wrap-search-block {
        padding: 11px 9px;
        margin-bottom: 4px;

        .search-block {
            align-items: center;
            display: flex;
            justify-content: space-between;
            position: relative;
        }

        .search-icon {
            height: 20px;
            position: absolute;
            width: 20px;
            z-index: 1;
            left: 7px;
        }

        .options-icon {
            cursor: pointer;
            height: 24px;
            width: 30px;
        }

        .search-input-chat {
            padding: 0;

            .md-input {
                padding: 0 5px 0 27px;
            }
        }

        .md-errors-spacer {
            height: 0;
        }
    }

    .search-result {
        color: #989a9f;
        padding: 10px;

        h4 {
            font-size: 13px;
            font-weight: 500;
            margin-bottom: 10px;
        }

        .search-result-item {
            background: #fafbfd;
            border-radius: 5px;
            border: 1px solid #e8f2ff;
            font-size: 11px;
            font-weight: 400;
            padding: 5px;
            margin-bottom: 10px;

            p {
                margin: 5px 0 0;
            }
        }
    }

    .bookmark-tab {
        align-items: center;
        cursor: pointer;
        display: flex;
        font-weight: 500;
        color: $TextGreyColor;
        padding: 5px 12px;
        margin-bottom: 4px;

        &.selected {
            background: $MainMenuBlueColor;
            color: #fff;

            .message-text {
                color: #ddd !important;
            }
        }

        .svg-icon {
            margin-right: 6px;
            height: 24px;
            width: 24px;
        }

        .bookmark-icon {
            fill: #d62516;
            margin-top: 6px;
        }
    }

    .message-icon {
        height: 20px;
        width: 20px;
    }

    .direct-icon {
        fill: green;
    }

    .thread-item {
        border-top: 1px solid #eeecec;
        padding: 5px 14px 7px;
        cursor: pointer;

        &.selected {
            background: #1e20a8;
            color: #fff;

            .top_txt .guest-name {
                color: #fff;
                font-weight: 700;
            }

            .message-text {
                color: #ddd !important;
            }
        }

        &:last-child {
            border-bottom: 1px solid #eeecec;
        }

        md-icon {
            &.active {
                color: #ffbb3e;
            }
        }
    }

    .item-user-chat {
        cursor: pointer;
        padding: 5px 0 5px 39px;
        display: flex;
        font-weight: 500;
        align-items: center;
        position: relative;

        &.selected {
            background: #1e20a8;
            color: #fff !important;

            .message-text {
                color: #ddd !important;
            }
        }

        &.unread {
            color: #2b78e4;
            font-weight: 700;
        }

        img {
            height: 28px;
            width: 28px;
        }

        .room-thread-action {
            align-items: center;
            display: flex;
            position: absolute;
            right: 5px;
        }

        .chat-entry-icon, .mentioned-icon {
            height: 16px;
            width: 16px;
            fill: #54a702;
        }

        .mentioned-icon {
            fill: #2b78e4;
            margin-left: 5px;
        }

        .lock-icon, .unlock-icon {
            height: 20px;
            margin-right: 5px;
            width: 20px;
        }

        .icon-status-user {
            background: #656a72;
            border-radius: 50%;
            border: 2px solid #fff;
            display: inline-block;
            height: 10px;
            margin: 18px 8px 0 -7px;
            width: 10px;

            &.online {
                background: #54a702;
            }
        }
    }

    .cp-Panels {
        .arrow-icon {
            margin-right: 6px;
        }

        .internal-inner-panel {
            //padding: 0 10px 0 14px;
            padding: 0;
        }

        .menu-link {
            align-items: center;
            display: flex;

            .svg-icon {
                margin-right: 6px;
                height: 24px;
                width: 24px;
            }
        }

        .cp-Panel-toggle {
            padding: 0;
            align-items: center;

            > .alignCenter {
                padding: 10px 12px;
            }

            .radio-icon, .direct-icon {
                margin-right: 6px;
            }

            .md-button {
                height: 26px;
                margin: 4px 5px 0 0;
                padding: 0;
                width: 26px;
            }
        }

        .plus-circle-icon {
            height: 24px;
            width: 24px;
        }

        .top_txt {
            align-items: center;
            display: flex;
            justify-content: space-between;

            .guest-name {
                color: #121416;
                font-weight: 500;
            }

            .message-time {
                font-size: 11px;
            }
        }

        .property-text {
            font-size: 11px;
            font-weight: 500;
            margin-bottom: 7px;
        }

        .bottom_txt {
            justify-content: space-between;
            display: flex;
            font-size: 12px;

            .message-text {
                color: #5d5e64;
                font-size: 15px;
                font-weight: 500;
                flex: 1;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                margin-right: 5px;
            }
        }
    }
}

.lock-icon {
    fill: #d62516;
}

.unlock-icon {
    fill: #54a702;
}

.visitor-info, .team-info, .user-info {
    //background-color: #f9fdff;
    width: 25%;
    padding: 5px;

    .user-avatar {
        text-align: center;
        margin-bottom: 10px;

        p {
            font-size: 18px;
            font-weight: 500;
            margin: 5px 0 14px;
        }
    }

    .user-name {
        color: #121416;
        font-weight: 500;
        margin-bottom: 5px;
    }

    .lock-icon, .unlock-icon {
        height: 20px;
        margin-right: 5px;
        width: 20px;
    }

    .add-member {
        display: flex;
        justify-content: flex-end;

        .md-button {
            margin: 0;
            padding: 0;
            width: auto;
        }

        span {
            color: #2b78e4;
            font-size: 12px;
            font-weight: 700;
        }
    }

    .plus-icon {
        height: 20px;
        margin-right: 2px;
        vertical-align: middle;
        width: 20px;
    }

    .created-date {
        font-size: 14px;
        margin: 0 0 15px 25px;
    }

    .title-name {
        font-weight: 500;
        color: #121416;
        display: flex;
        margin-bottom: 10px;
    }

    .icon-status-user {
        vertical-align: text-top;
    }

    .delete-user {
        cursor: pointer;
        display: none;
        position: absolute;
        right: 10px;
        top: 8px;

        .svg-icon {
            height: 20px;
            width: 20px;
        }
    }

    .list-member {
        .item-member {
            margin-bottom: 7px;
            position: relative;

            &:hover {
                .delete-user {
                    display: block;
                }
            }

            .name {
                font-weight: 400;
                font-size: 12px;
                color: #000;
            }

            .md-avatar {
                margin-right: 2px;
                vertical-align: middle;
            }
        }
    }

    .info-item {
        background-color: #fff;
        border: 2px solid #f5f5f5;
        border-radius: 3px;
        padding: 5px 10px;
        margin-bottom: 10px;
        //font-size: 12px;

         .liquid-container, .liquid-child {
            overflow: visible;
        }

        .primary-button {
            width: 100%;
        }

        .description {
            margin-left: 25px;
        }

        .count-form {
            margin-right: 10px;
        }

        .progress-form {
            margin-bottom: 20px;
            position: relative;

            .bar {
                background: #54a702;
                border-radius: 2px;
                height: 8px;
                opacity: .3;
            }

            .line {
                background: #54a702;
                border-radius: 2px;
                position: absolute;
                left: 0;
                top: 0;
                height: 8px;
                width: 0;
            }
        }

        .email-icon, .phone-icon, .copy-icon {
            height: 16px;
            margin-right: 4px;
            vertical-align: middle;
            width: 16px;
        }

        .pin-icon, .globe-icon {
            margin-right: 4px;
            height: 16px;
            vertical-align: middle;
            width: 16px;
        }

        .profile-item {
            display: flex;
            align-items: center;
            margin-bottom: 12px;

            .svg-icon {
                margin-right: 7px;
                height: 20px;
                width: 20px;
            }
        }

        span {
            .svg-icon {
                vertical-align: bottom;
            }

            &.shopify {
                color: #5E8E3E;
            }

            &.bigcommerce {
                color: #32323B;
            }

            &.username {
                margin-left: 20px;
                flex: 1;
                color: $LinkColor;
            }
        }
    }

    .exchanges-section {
        min-height: 332px;

        .liquid-container {
            padding: 15px 0;
        }

        .img-item {
            width: 31%;
            margin-right: 3%;
            height: 100px;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;

            &:nth-child(3n + 0) {
                margin-right: 0;
            }
        }

        .doc-item {
            display: flex;
            margin-bottom: 10px;

            .doc-item-icon {
                margin-right: 5px;
            }

            .svg-icon {
                height: 20px;
                width: 20px;
            }
        }

        .links-tab {
            .link {
                display: inline-block;
                margin-bottom: 10px;
            }
        }
    }

    label {
        //font-size: 12px;
        margin-right: 5px;
        width: 100px;
    }

    .md-input {
        border-radius: 5px;
        background: #fff;
        padding: 0 5px;
    }

    .visitor-info-wrap {
        padding: 13px 15px;
    }

    .visitor-info-header {
        border-bottom: 2px solid #fbfbfb;
        color: #606a73;
        font-size: 18px;
        margin: 0;
        padding: 0 0 12px;

        .info {
            vertical-align: middle;
        }
    }

    .visitor-avatar {
        color: #121416;
        text-align: center;

        p {
            font-weight: 500;
            margin: 5px 0 14px;
        }
    }

    .cp-Panel-body-inner {
        margin-top: 10px;
    }
}

.pinned-items {
    border: solid 2px #eff0f0;
    border-radius: 8px;
    display: flex;
    margin-bottom: 5px;
    padding: 10px;
    position: relative;

    .md-avatar {
        margin-right: 10px;
    }

    .content {
        overflow: hidden;
    }

    .unpin-button {
        margin: 0;
        padding: 0;
        position: absolute;
        right: 2px;
        top: 2px;
        width: 24px;
        height: 24px;

        .close-icon {
            height: 16px;
            vertical-align: middle;
            width: 16px;
        }
    }

    .top_content {
        span {
            margin-right: 7px;
        }
    }

    .inner-text {
        font-weight: 500;
        margin-top: 7px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    img {
        max-width: 100%;
        max-height: 80px;
    }
}

.message-section {
    background-color: #f9fdff;
    flex: 1;
    display: flex;
    flex-direction: column;
    border-right: 1px solid rgba(218, 218, 218, 0.5);

    .title-name {
        font-weight: 500;
        font-size: 16px;
        color: #231f20;
        position: relative;
    }

    .typing-text {
        color: #989a9f;
        display: none;
        font-size: 11px;
        font-weight: 400;
        margin: 0 5px;
        white-space: nowrap;
        overflow: hidden;
        opacity: .6;
        text-overflow: ellipsis;
    }

    .lock-icon, .unlock-icon {
        height: 24px;
        margin-right: 8px;
        width: 24px;
    }

    .participants-count {
        color: #231f20;
        display: flex;
        font-size: 14px;
        font-weight: 500;
        margin-left: 32px;

        .people-icon {
            height: 16px;
            margin-right: 4px;
            width: 16px;
        }
    }

    .application-loading-wrapper {
        margin: auto;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        top: 0;
        display: flex;
        bottom: 0;
        right: 0;
        left: 0;

        p {
            color: #989a9f;
        }
    }

    .message-section-title {
        margin-bottom: 1px;
        justify-content: space-between;
        display: flex;
        align-items: center;
        height: 60px;
        padding: 0 10px;
        position: relative;

        .md-icon-button {
            margin: 0 0 0 15px;
            padding: 0;
            height: 24px;
            width: 24px;
        }

        .checkmark-icon, .swap-icon, .delete-icon, .info-icon {
            height: 24px;
            width: 24px;

            &.opened {
                fill: $AdminMenuBlueColor;
            }

            &:hover {
                fill: #2b78e4;
            }
        }

        .whosread {
            > span {
                margin-right: 5px;
            }

            span {
                line-height: 25px;
                vertical-align: top;
            }

            .all-users {
                position: relative;
                display: inline-block;
                min-width: 25px;
                height: 25px;
                border-radius: 12px;
                vertical-align: top;
                background-color: #d0d0d0;
                cursor: pointer;
                text-align: center;

                span {
                    display: inline-block;
                    padding: 5px;
                }

                .user-list {
                    position: absolute;
                    width: 200px;
                    top: 25px;
                    left: -90px;
                    max-height: 600px;
                    overflow-x: hidden;
                    overflow-y: auto;
                    background-color: #fff;
                    border: solid 1px #e7e7e7;
                    z-index: 10;
                    cursor: default;
                    text-align: left;

                    .item {
                        margin: 5px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        width: 100%;

                        .md-avatar {
                            margin-right: 5px;
                        }
                    }
                }
            }
        }
    }
}

.messages {
    flex: 1;

    .bookmark-banner {
        margin: 10px;
        padding: 10px 20px;
        border: solid 1px #ececec;
        border-radius: 10px;
        background-color: #DFEBFD;
        color: $TextBlackColor;

        b {
            display: block;
            margin-bottom: 10px;
        }
    }

    .bookmark {
        margin: 10px;
        padding: 10px 20px 0 20px;
        border: solid 1px #2b78e4;
        border-radius: 10px;
        background-color: #fff;

        &:hover {
            background-color: #effaff;
        }

        .link-to-msg {
            cursor: pointer;
            font-weight: 500;

            &:hover {
                text-decoration: underline;
            }
        }

        .lock-icon,
        .unlock-icon {
            width: 16px;
            margin: 0;
            height: 16px;
            vertical-align: bottom;
        }
    }

    .reminder {
        background-color: $InputBorderColor;

        .svg-icon {
            margin: 0;
            vertical-align: bottom;
        }
    }

    &.hidden {
        .tse-scroll-content {
            overflow: hidden;
        }
    }

    .wrap-title-date {
        text-align: center;
        margin: 15px 0;
        position: relative;

        &:before {
            border-top: 1px solid #eeecec;
            content: '';
            position: absolute;
            width: 100%;
            left: 0;
            top: 12px;
        }
    }

    .title-date {
        font-size: 11px;
        color: #989a9f;
        display: inline-block;
        position: relative;
        z-index: 1;
        background: #f9fdff;
        padding: 0 30px;
    }

    .no-messages {
        position: absolute;

        img {
            max-width: 100%;
        }
    }

    .user-group {
        color: #2b78e4;
        outline: none;
        //margin-left: 5px;
    }

    .msg {
        height: auto;
        min-height: auto;
        padding: 10px 15px;
        position: relative;

        &:hover {
            background: #effaff;

            .reply-icon-message {
                display: block;
            }
        }

        &.pinned {
            background-color: #FDF9E9;
        }

        &.internal-note {
            background: #fdf9e9;
        }

        .smile-plus-icon {
            height: 20px;
            margin-left: 10px;
            width: 20px;
        }

        .top-info-msg {
            font-size: 10px;

            .name {
                color: #121416;
                font-size: 15px;
                font-weight: 500;
                margin-right: 10px;
            }

            .read-msg, .edited-msg {
                color: #43d854;

                &:before {
                    content: '';
                    width: 2px;
                    margin: 0 6px 0 4px;
                    border-radius: 50%;
                    vertical-align: middle;
                    background: $primary-color-grey;
                    display: inline-block;
                    height: 2px;
                }
            }

            .edited-msg {
                color: #989a9f;
            }
        }

        .picker-popup-wrapper {
            box-shadow: 0 1px 10px 0 rgba(0, 0, 0, .3);
            background-color: #fff;
            border-radius: 5px;
            top: 52px;
            padding-bottom: 5px;
            position: absolute;
            right: 0;
            width: 240px;
            z-index: 1000;

            &.inline {
                right: auto;
                top: auto;
            }

            .emojis-title {
                border-bottom: 1px solid rgba(0, 0, 0, .2);
                padding: 5px;
                white-space: nowrap;
                overflow: auto;
            }

            .wrap-icon {
                border-radius: 5px;
                cursor: pointer;
                display: inline-block;
                padding: 3px;

                &.active, &:hover {
                    background-color: #dfebfd;
                }
            }

            .group-icon {
                height: 24px;
                width: 24px;
            }

            .emojis-list {
                height: 210px;
                padding: 5px 10px;
                overflow: auto;

                > div {
                    background-repeat: no-repeat;
                    cursor: pointer;
                    image-rendering: optimizeQuality;
                    height: 24px;
                    display: inline-block;
                    padding: 5px;
                    text-indent: -9999em;
                    width: 24px;
                    transform: scale(1);

                    &:hover {
                        transform: scale(1.15);
                    }
                }
            }
        }

        .content {
            .status-send {
                position: relative;
                margin: 11px 0 0 18px;

                &.active {
                    md-icon {
                        color: #007bff;
                    }
                }

                md-icon {
                    display: block;
                }
            }
        }

        .inner-text {
            color: #5d5e64;
            margin: 0;
            word-wrap: break-word;

            .message-deleted {
                background: #f0f0f0;
                border-radius: 8px;
                color: #989a9f;
                display: inline-block;
                font-size: 13px;
                font-weight: 500;
                padding: 9px 10px;
            }

            .user-group {
                background: #f2c4b2;
                border-radius: 5px;
                color: #989a9f;
                font-size: 12px;
                font-weight: 500;
                padding: 2px 5px;
            }

            > a {
                display: flex;
            }

            .svg-icon {
                margin-right: 5px;
                height: 20px;
                width: 20px;
            }

            .product-list {
                .preview {
                    display: flex;
                    flex-wrap: nowrap;
                    align-items: center;
                    margin-bottom: 10px;

                    .product-image {
                        display: block;
                        width: 80px;
                        height: 80px;
                        background-repeat: no-repeat;
                        background-size: cover;
                        vertical-align: middle;
                    }

                    .info {
                        flex: 10;
                        padding-left: 10px;

                        .title {
                            font-weight: normal;
                            margin-bottom: 10px;
                        }

                        .price {
                            font-weight: 600;
                        }
                    }
                }
            }
        }

        .emojis {
            align-items: center;
            display: inline-flex;
            margin: 5px 0 0 55px;
            vertical-align: bottom;
            width: 50%;
            flex-wrap: wrap;

            .emoji-wrap {
                align-items: center;
                background-color: #2b78e4;
                border-radius: 5px;
                margin: 0 2px 2px 0;
                position: relative;
                cursor: pointer;
                display: flex;
                padding: 2px 4px;

                & + .md-icon-button {
                    margin: 0 0 0 7px;
                    padding: 0;
                    height: auto;
                    width: auto;
                    line-height: normal;
                }
            }

            .emoji {
                margin-right: 4px;
                background-repeat: no-repeat;
                image-rendering: optimizeQuality;
                height: 24px;
                text-indent: -9999em;
                width: 24px;
            }

            .emoji-count {
                color: #fff;
                font-size: 13px;
                font-weight: 500;
                margin-left: 2px;
            }

            .emoji-author-field {
                padding: 5px;
                bottom: 30px;
                border-radius: 6px;
                background-color: #fff;
                box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
                position: absolute;
                z-index: 1000000;
                margin-top: -60px;
                text-align: left;

                > div {
                    white-space: nowrap;
                }
            }
        }

        .text {
            font-weight: 500;
            margin-top: 5px;

            .title-note {
                color: #2b78e4;
                cursor: pointer;
                font-size: 13px;
                font-weight: 500;
                text-decoration: underline;
                margin: 0 0 5px;
                display: none;
            }

            .reply {
                cursor: pointer;
                position: absolute;
                right: 21px;
                bottom: 1px;
                color: #2161a5;
                font-size: 13px;
                font-weight: 500;
                display: none;
            }

            &.note {
                .user-name, .reply {
                    display: inline-block;
                    color: #94A4AF;
                }

                .title-note {
                    display: block;
                }
            }

            .user-name {
                background: #f2c4b2;
                border-radius: 5px;
                display: none;
                font-size: 12px;
                font-weight: 500;
                padding: 2px 5px;
            }

            .replyTo {
                border-left: 3px solid rgba(0, 0, 0, .12);
                margin-top: 5px;
                padding: 4px 10px 6px 15px;

                .reply-info {
                    img {
                        float: none;
                        height: 25px;
                        display: inline-block;
                        margin: 0 6px 10px 0;
                        vertical-align: top;
                        width: 25px;
                    }

                    span {
                        font-weight: bold;
                    }
                }

                p {
                    margin: 0;
                }
            }

            .image-message {
                width: 300px;
                display: block;

                .svg-icon {
                    margin-right: 5px;
                    height: 20px;
                    width: 20px;
                }

                img {
                    width: 100%;
                    height: auto;
                }
            }
        }

        .star-icon-message, .reply-icon-message {
            display: none;
            margin: 0;
            position: absolute;
            right: 5px;
            top: 0;

            .reply-icon {
                height: 24px;
                width: 24px;
            }
        }

        md-icon {
            &.active {
                color: #ffbb3e;
                display: block;
            }
        }

        .smile-icon {
            height: 21px;
            width: 21px;
        }

        .message {
            display: flex;

            .md-avatar {
                margin-right: 20px;
            }

            .pin {
                font-size: 11px;
                margin-bottom: 7px;
            }

            .office-pin-icon {
                height: 12px;
                margin-right: 2px;
                vertical-align: middle;
                width: 11px;
            }

            .reactions {
                background-color: #fff;
                box-shadow: 0 1px 10px 0 rgba(0, 0, 0, .3);
                border-radius: 5px;
                display: none;
                top: -32px;
                padding: 4px 10px;
                position: absolute;
                right: 20px;
                z-index: 1;

                .trash-icon, .edit-icon {
                    height: 24px;
                    width: 24px;
                }

                .pushpin-icon {
                    height: 20px;
                    width: 20px;

                    &.red {
                        fill: red;
                    }
                }

                .md-button {
                    box-sizing: content-box;
                    margin: 0;
                    height: 24px;
                    width: 24px;
                }

                .remind-trigger {
                    display: inline-block;
                    position: relative;
                    width: 40px;
                    padding-left: 10px;

                    .svg-icon {
                        position: absolute;
                        top: -15px;
                        cursor: pointer;
                    }
                }
            }
        }

        &:hover {
            .reactions {
                display: block;
            }
        }

        .date-message {
            text-align: right;
        }
    }
}

.chat-form {
    font-weight: 400;

    .title-create-room {
        line-height: 1.4;
        margin-bottom: 20px;
    }

    .switch-title {
        font-style: italic;
    }

    form {
        .label {
            margin-bottom: 5px;
        }

        .paper-switch {
            &.md-checked {
                .md-container:before {
                    content: "Public";
                    left: 8px;
                }

                .md-bar {
                    background: #54a702;
                }

                .md-thumb {
                    background: #fff;
                }
            }

            .md-thumb-container {
                width: 47px;
            }

            .md-container {
                margin-right: 15px;
                width: 70px;

                &:before {
                    content: "Private";
                    color: #fff;
                    font-size: 11px;
                    font-weight: 500;
                    position: absolute;
                    right: 8px;
                    top: -2px;
                    z-index: 10;
                }

                .md-bar {
                    width: 100%;
                }
            }
        }
    }

    .info-icon {
        height: 14px;
        width: 14px;
    }

    h4 {
        font-weight: normal;
        margin-bottom: 20px;
    }
}

.ecommerce-info {
    min-height: 400px;

    md-tabs .md-tab {
        padding: 10px 15px 0;
    }

    md-pagination-wrapper {
        height: 36px;
    }

    .liquid-container {
        padding: 0;
    }

    .link {
        &.back-to-orders {
            color: $MainMenuBlueColor !important;
        }
    }

    .order-details {
        width: 100%;
        margin-top: 20px;
        margin-bottom: 10px;

        .primary-button {
            margin-left: 10px;
        }

        .cp-Panel {
            border-top: solid 1px #DADADA;
            margin-top: 10px;
        }

        .cp-Panel-toggle {
            color: $TextLightGreyColor;

            .svg-icon {
                vertical-align: text-bottom;
            }

            &.cp-is-open {
                color: #508AEE;

                .svg-icon {
                    fill: #508AEE;
                }
            }
        }
    }

    .order-list {
        max-height: 400px;
        overflow-y: auto;

        .order-item {
            border-bottom: solid 1px #DADADA;
            margin-top: 20px;
            padding-right: 10px;

        }
    }

    .order-id {
        color: $LinkColor;
    }

    .data {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
        width: 100%;
        margin-bottom: 10px;

        &.orders {
            margin-top: 10px;
            border-bottom: solid 1px #ADADAD;
            padding: 10px 0;
            margin-bottom: 10px;
        }

        .status-date {
            width: 100%;
            margin-bottom: 10px;
        }

        .refund_quantity {
            width: 80px;
            margin: 0;
        }

        .product-image {
            display: inline-block;
            width: 50px;
            height: 50px;
            background-repeat: no-repeat;
            background-size: contain;
            vertical-align: middle;
            margin-right: 15px
        }
    }

    .deselect-products {
        height: 25px;
        line-height: 25px;
        text-align: right;
        cursor: pointer;

        &:hover {
            color: $AdminMenuBlueColor;
        }
    }

    .share-btn {
        button {
            margin: 0;
        }

        span {
            display: inline-block;
            height: 32px;
            line-height: 32px;
        }
    }

    .panel {
        border-top: solid 1px #DADADA;
        margin-bottom: 10px;
        padding-top: 10px;

        &.product-list {
            border-top: none;
            padding-top: 0;
            padding-bottom: 20px;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            width: 100%;
            max-width: 100%;
            max-height: 500px;
            overflow-y: auto;

            .product-image {
                display: block;
                width: 100%;
                height: 100px;
                background-repeat: no-repeat;
                background-size: cover;
                vertical-align: middle;
                border: solid 2px transparent;
            }

            .preview {
                width: 33%;
                padding: 0 5px;
                max-width: 33%;
                cursor: pointer;

                &.selected {
                    .product-image {
                        border-color: $AdminMenuBlueColor;
                    }
                }

                .info {
                    position: relative;
                    height: 60px;

                    .wrapper {
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        background-color: $BodyColor;
                        z-index: 1;

                        .title {
                            width: 100%;
                            max-width: 100%;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            overflow: hidden;
                            margin-top: 5px;
                            margin-bottom: 5px;
                        }

                        .price {
                            font-weight: 600;
                            margin-top: 5px;
                            margin-bottom: 5px;
                        }
                    }
                }

                &:hover {
                    .info {
                        .wrapper {
                            .title {
                                text-overflow: unset;
                                white-space: unset;
                            }
                        }
                    }
                }
            }
        }
    }

    .status-select {
        display: flex;
        border: solid 1px #b0dcf7;
        border-right: none;
        margin-bottom: 10px;
        text-align: center;

        .item {
            cursor: pointer;
            flex: 1;
            padding: 10px 0;
            border-right: solid 1px #b0dcf7;

            &.selected {
                cursor: default;
                color: white;
                background-color: #DADADA;
            }
        }
    }

    .order-tags {
        .tag-item {
            background: #ddebff;
            border-radius: 5px;
            color: #2b78e4;
            display: inline-block;
            font-size: 13px;
            font-weight: 400;
            margin: 0 4px 5px 0;
            padding: 4px 8px;

            &.closed {
                background-color: #34A85355;
                color: #34A853;
            }

            &.cancelled {
                background-color: #FBBC0555;
                color: #FBBC05;
            }
        }
    }
}

.cancel-order-sidenav,
.refund-order-sidenav {
    span {
        &.shopify {
            color: #5E8E3E;
        }

        &.bigcommerce {
            color: #32323B;
        }

        .platform-logo {
            height: 16px;
            width: 16px;
            vertical-align: text-bottom;
        }

        &.username {
            text-transform: none;
            text-align: right;
            font-size: 13px;
            margin-left: 20px;
            color: $LinkColor;
        }
    }
}

.braincheck-avatar {
    width: 35px !important;
    height: 35px !important;
    margin-right: 20px !important;
}

.star-switch {
    h4 {
        color: #212529;
        font-size: 18px;
        margin: 22px 0 5px;
        padding: 0 9px;
    }

    .close-button {
        margin: 0 !important;
        position: absolute;
        right: 20px;
        top: 14px;
    }

    .star-date {
        position: absolute;
        left: 35px;
        top: 14px;
        margin: 0;
    }

    .list-reply-message {
        padding: 10px;

        li {
            border-bottom: 2px solid #fbfbfb;
            color: #bdbac2;
            padding: 12px 0;
            position: relative;

            &:hover {
                border-color: #007bff;

                .jump, .star-icon-message {
                    display: block;
                }
            }
        }

        .star-text {
            margin: 5px 0 0;
        }

        .star-icon-message {
            position: absolute;
            right: -14px;
            display: none;
            top: 2px;

            md-icon {
                color: #ffbb3e;
            }
        }

        .jump {
            border: 1px solid;
            border-radius: 5px;
            cursor: pointer;
            display: none;
            padding: 0 5px 2px;
            position: absolute;
            right: 32px;
            top: 12px;

            &:hover {
                color: #ffbb3e;
            }
        }
    }
}

.list-guest {
    .guest-item {
        border: 1px solid #ffd088;
        border-radius: 5px;
        box-sizing: border-box;
        cursor: pointer;
        padding: 11px;
        height: 107px;
        position: relative;
        margin: 0 0 7px;
        transition: .3s;
        overflow: hidden;

        &:hover {
            border-color: #286efa;
            background-color: rgba(0, 0, 0, .3);
            height: 137px;

            .guest-link, .associate {
                display: block;
                opacity: 1;
            }
        }
    }

    .guest-link {
        border: 1px solid transparent;
        border-radius: 4px;
        display: none;
        color: #fff;
        position: absolute;
        right: 15px;
        bottom: 7px;
        transition: .2s;
        padding: 3px 8px 6px;
        z-index: 2;
        opacity: 0;
        width: 70px;
        text-align: center;

        &:hover {
            border-color: #fff;
        }
    }

    .associate {
        border: 1px solid transparent;
        border-radius: 4px;
        color: #fff;
        margin: 0;
        padding: 6px 8px;
        position: absolute;
        z-index: 2;
        left: 15px;
        transition: .2s;
        min-width: auto;
        line-height: 1;
        opacity: 0;
        bottom: 7px;

        &:hover {
            border-color: #fff;
        }
    }

    ul {
        margin: 0;

        li {
            padding: 0;
            margin: 0 0 5px;
        }
    }

    .guest-title {
        display: inline-block;
        width: 30%;
    }
}

.popup-reply {
    width: 400px;

    .md-dialog-content {
        padding: 15px;
    }

    .reply-block {
        border-left: 3px solid rgba(0, 0, 0, .12);
        color: #c1bebe;
        padding: 0 10px 2px;
    }

    textarea {
        border: none !important;
    }
}
