@import 'common/colors';

html,
body {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
}

body {
    background-color: #fff;
    color: $TextLightGreyColor;
    font-size: 15px;
    cursor: default;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'circular', '-apple-system', 'BlinkMacSystemFont', 'Segoe UI', 'sans-serif';
}

.dz-preview {
    display: none;
}

a {
    color: $TextLightGreyColor;
    text-decoration: none;
}

ul {
    list-style: none;
}

.main-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;

    .liquid-container {
        transform: none;
    }
}

.page-content {
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100%;
    max-height: 100%;
    margin-left: -12px;
    background-color: $BodyColor;
    overflow: auto;
}

.page-header {
    align-items: center;
    border-bottom: solid 1px #ececec;
    display: flex;
    height: 62px;
    z-index: 1;

    .dynamic-content {
        width: 250px;
    }

    .global-search {
        flex: 10;

        .search-icon {
            width: 24px;
            height: 24px;
            fill: $TextLightGreyColor;
        }
    }

    .onboard-title {
        margin-right: 15px;
        display: flex;
    }

    .wrapper-user-popup {
        border-left: solid 1px #ececec;
        padding: 0 29px;

        .user-name {
            color: #121416;
            margin-bottom: 5px;
            font-size: 15px;
        }

        .user-status-text {
            font-size: 13px;
        }

        .menu-icon {
            width: 20px;
            height: 20px;
            margin: 0 8px;
            fill: $TextLightGreyColor;
        }

        .apps-popup-menu {
            margin-top: 10px;

            ul {
                li {
                    margin-bottom: 12px;

                    &:hover {
                        svg {
                            fill: $AdminMenuBlueColor !important;
                        }

                        a {
                            color: $AdminMenuBlueColor;
                        }
                    }

                    a {
                        display: flex;
                        align-items: center;
                    }
                }
            }

            .ember-basic-dropdown-trigger {
                cursor: pointer;
                display: flex;
                align-items: center;

                &:hover {
                    color: $AdminMenuBlueColor;

                    svg {
                        fill: $AdminMenuBlueColor;
                    }
                }
            }
        }

        .sign-out-btn-place {
            padding-top: 17px;
            border-top: solid 1px #E5E5E5;
            margin-top: 6px;

            .sign-out-btn {
                display: inline-flex;
                align-items: center;
                cursor: pointer;

                &:hover {
                    color: $AdminMenuBlueColor;

                    svg {
                        fill: $AdminMenuBlueColor;
                    }
                }
            }
        }

        .user-menu-icon {
            height: 40px;
            line-height: 40px;
            min-width: 40px;
            margin: 10px 0;
            padding: 0;
            width: 40px;

            &:hover {
                background-color: transparent !important;
            }

            img {
                border: 1px solid #2B78E4;
                box-sizing: content-box;
            }

            .user-status-icon {
                border: 1px solid #fff;
                border-radius: 50%;
                height: 10px;
                position: absolute;
                right: 0;
                bottom: 2px;
                width: 10px;

                &.online {
                    background: green;
                }

                &.away {
                    background: #FFBF00;
                }

                &.offline {
                    background: #cacaca;
                }
            }
        }
    }
}

.page-wrapper {
    flex: 1;
    display: flex;
    flex-direction: row;

    .admin-menu {
        &.closed {
            width: 0;
        }
    }

    &.admin {
        .page-title {
            flex-direction: row;
        }
    }

    &.dashboard {
        .page-inner-content {
            margin: 0;
        }

        .dashboard-page {
            padding: 20px;
        }
    }

    .page-title {
        flex-direction: column;

        .text {
            flex: 1;
        }
    }

    .title-tasks,
    .buttons-panel {
        width: 100%;
    }

    .title-tasks,
    .page-title {
        display: flex;
        //align-items: center;

        .primary-button,
        .primary-2-button,
        .secondary-button,
        .secondary-2-button {
            display: inline-flex;
            margin: 0 0 0 15px;

            .svg-icon {
                margin-right: 4px;
            }
        }

        .primary-button {
            .paper-icon {
                color: #fff;
            }
        }
    }
}

.admin-menu {
    border-right: 1px solid #ececec;
    transition: .3s;
    width: 230px;

    &.user-profile {
        width: 370px;
        background: none;
    }

    .settings-menu-heading {
        margin: 5px 6px 14px;
    }

    .profile-settings-menu {
        .cp-Panel-toggle {
            margin-bottom: 15px;
            text-transform: none;
            font-size: 14px;

            .arrow-forward {
                top: 2px;
            }
        }
    }
}

.page-inner-wrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
    position: relative;
    transition: .3s;

    .page-title {
        position: relative;
        margin: 20px 18px 0 20px;
        min-height: 34px;

        .expand-button {
            width: 24px;
            height: 24px;
            padding: 0;
            margin-right: 11px !important;
        }

        h2 {
            color: $TextBlackColor;
            display: inline-block;
            font-size: 16px;
            font-weight: 500;
            vertical-align: middle;
            text-transform: uppercase;
            margin-right: 10px;

            &.task-title {
                padding: 10px 15px;
                float: left;
            }
        }

        &.blue {
            h2 {
                //color: $Primary2ButtonColor;
            }
        }
    }

    .page-inner-content {
        flex: 1;
        margin: 20px;

        .action-button {
            margin-top: 10px;
        }
    }

    .page-footer {
        margin: 0 20px 20px 20px;

        .primary-button {
            margin-left: 18px;
        }
    }

    &.wrap {
        box-shadow: none;
        margin: 0;
    }

    .md-button.md-icon-button {
        margin: 0;
    }

    .description {
        margin-bottom: 15px;
    }

    .title-avatar {
        margin-right: 15px;
    }
}

.flex-1 {
    flex: 1;
}

